import "./styles.css";
import { Component } from "react";
import { lightTheme, darkTheme } from "./theme";
import { ThemeProvider } from "styled-components";
import { GlobalStyles } from "./global";
import moon from "./moon.png";
import sun from "./sun.png";
import loading from "./loading10.gif";
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      header: <Header />,
      switch: <SwitchButton theme={this.theme} />,
      body: (
        <Body
          handleSelect={this.handleSelect.bind(this)}
          handleSearch={this.handleSearch.bind(this)}
        />
      ),
      countries: <Countries indexHandler={this.indexHandler.bind(this)} />,
      data: [],
      index: [],
      region: "",
      regionData2: [],
      searchData3: [],
    };
  }

  async handleSearch(event) {
    if (
      //event.target.value.split("").length === 0 ||
      this.state.searchData3.length === 0 ||
      !Boolean(event.target.value.match(/[aA-zZ]/g))
    ) {
      return this.setState({
        searchData3: this.state.data,

        countries: <Countries indexHandler={this.indexHandler.bind(this)} />,
        body: (
          <Body
            handleSelect={this.handleSelect.bind(this)}
            handleSearch={this.handleSearch.bind(this)}
          />
        ),
      });
    } else {
      this.setState({
        countries: (
          <div style={{ display: "flex", alignItems: "center" }}>
            <img id="loading" src={loading} alt="loading"></img>
          </div>
        ),
      });
    }

    await fetch(
      "https://restcountries.eu/rest/v2/name/" +
        event.target.value.toLowerCase()
    )
      .then((res) => res.json())
      .then((searchData3) => {
        this.setState({
          searchData3,
        });
      })
      .catch((err) => {
        console.log(err);
      });

    this.setState({
      header: <Header />,
      body: (
        <Body
          handleSelect={this.handleSelect.bind(this)}
          handleSearch={this.handleSearch.bind(this)}
          handleSearchIndividual={this.handleSearchIndividual.bind(this)} //es
        />
      ),
      countries:
        this.state.searchData3.length >= 1 ? (
          <div id="search-country">
            {this.state.searchData3.map((country, index) => (
              <li key={index} id="all-country-li">
                <img
                  id="search-country-img"
                  src={country.flag}
                  data-index={index}
                  onClick={this.handleSearchIndividual.bind(this)}
                  alt={country.name}
                ></img>
                <h3
                  data-index={index}
                  onClick={this.handleSearchIndividual.bind(this)}
                  id="test"
                >
                  {country.name}
                </h3>
                <p>Population: {country.population}</p>
                <p>Region: {country.region}</p>
                <p>Capital: {country.capital}</p>
              </li>
            ))}
          </div>
        ) : (
          <h1>Not Found!</h1>
        ),
      data: this.state.data,
      index: [],
      region: "",
      regionData2: this.state.regionData2, //es
      searchData3: this.state.searchData3,
    });
  }

  async handleSelect(event) {
    await fetch("https://restcountries.eu/rest/v2/region/" + event.target.value)
      .then((res) => res.json())
      .then((regionData2) => {
        this.setState({
          regionData2,
        });
      });

    this.setState({
      header: <Header />,
      body: (
        <Body
          handleSelect={this.handleSelect.bind(this)}
          handleSearch={this.handleSearch.bind(this)}
        />
      ),
      countries: "",
      region: (
        <Region
          data={this.state.regionData2}
          indexHandler={this.indexHandler.bind(this)}
        />
      ),
      data: this.state.regionData2, ///esss shesacvleli (this.state.data)
      regionData2: this.state.regionData2,
      index: [],
      searchData3: this.state.searchData3,
    });
  }
  handleSearchIndividual(event) {
    const index = event.target.getAttribute("data-index");

    this.state.index.unshift(index);
    this.setState({
      header: <Header />,
      body: "",
      searchData3: this.state.searchData3,
      index: [],
      //eseseseseseses
      countries: (
        <section>
          {this.state.searchData3.length > 1 ? (
            <div className="individual-card">
              <ul>
                {this.state.searchData3.reduce((country, index) => (
                  <li key={index} id="individual-li">
                    <div id="individual-card">
                      <div id="individual-img">
                        <img
                          src={this.state.searchData3[this.state.index].flag}
                          data-index={index}
                          onClick={this.props.indexHandler}
                          alt={country.name}
                          id="ind-image"
                        ></img>
                      </div>
                      <div id="individual-country-info">
                        <h3
                          data-index={index}
                          onClick={this.props.indexHandler}
                          id="test"
                        >
                          {this.state.searchData3[this.state.index].name}
                        </h3>
                        <div>
                          <p>
                            <span className="titleText">Native Name:</span>{" "}
                            {
                              this.state.searchData3[this.state.index]
                                .nativeName
                            }
                          </p>
                          <p>
                            <span className="titleText">Population:</span>{" "}
                            {
                              this.state.searchData3[this.state.index]
                                .population
                            }
                          </p>
                          <p>
                            <span className="titleText">Region:</span>{" "}
                            {this.state.searchData3[this.state.index].region}
                          </p>
                          <p>
                            <span className="titleText">Sub Region:</span>{" "}
                            {this.state.searchData3[this.state.index].subregion}
                          </p>
                        </div>
                        <div>
                          <p>
                            <span>Capital: </span>
                            {this.state.searchData3[this.state.index].capital}
                          </p>
                          <p>
                            <span className="titleText" key={index}>
                              Top Level Domain:
                            </span>{" "}
                            {this.state.searchData3[
                              this.state.index
                            ].topLevelDomain.map((dom, index) => dom)}
                          </p>
                          <p>
                            <span className="titleText" key={index}>
                              Currencies:
                            </span>{" "}
                            {this.state.searchData3[
                              this.state.index
                            ].currencies.map((cur, index) => " " + cur.name)}
                          </p>
                          <p>
                            {" "}
                            <span className="titleText" key={index}>
                              Languages:
                            </span>
                            {this.state.searchData3[
                              this.state.index
                            ].languages.map(
                              (lang, index) => " " + lang.name + ","
                            )}
                          </p>
                        </div>
                      </div>
                      <div className="border">
                        {" "}
                        <p className="titleText">Border Countries: </p>
                        {this.state.searchData3[this.state.index].borders.map(
                          (border, index) => (
                            <div id="border" key={index}>
                              {border}
                            </div>
                          )
                        )}
                      </div>
                      <button onClick={this.handleSubmit.bind(this)}>
                        Back
                      </button>
                    </div>
                  </li>
                ))}
              </ul>
            </div>
          ) : (
            <div className="individual-card">
              <ul>
                {this.state.searchData3.map((country, index) => (
                  <li key={index} id="individual-li">
                    <div id="individual-card">
                      <div id="individual-img">
                        <img
                          src={this.state.searchData3[this.state.index].flag}
                          data-index={index}
                          onClick={this.props.indexHandler}
                          alt={country.name}
                          id="ind-image"
                        ></img>
                      </div>
                      <div id="individual-country-info">
                        <h3
                          data-index={index}
                          onClick={this.props.indexHandler}
                          id="test"
                        >
                          {this.state.searchData3[this.state.index].name}
                        </h3>
                        <div>
                          <p>
                            <span className="titleText">Native Name:</span>{" "}
                            {
                              this.state.searchData3[this.state.index]
                                .nativeName
                            }
                          </p>
                          <p>
                            <span className="titleText">Population:</span>{" "}
                            {
                              this.state.searchData3[this.state.index]
                                .population
                            }
                          </p>
                          <p>
                            <span className="titleText">Region:</span>{" "}
                            {this.state.searchData3[this.state.index].region}
                          </p>
                          <p>
                            <span className="titleText">Sub Region:</span>{" "}
                            {this.state.searchData3[this.state.index].subregion}
                          </p>
                        </div>
                        <div>
                          <p>
                            <span className="titleText">Capital: </span>
                            {this.state.searchData3[this.state.index].capital}
                          </p>
                          <p>
                            <span className="titleText" key={index}>
                              Top Level Domain:
                            </span>{" "}
                            {this.state.searchData3[
                              this.state.index
                            ].topLevelDomain.map((dom, index) => dom)}
                          </p>
                          <p>
                            <span className="titleText" key={index}>
                              Currencies:
                            </span>{" "}
                            {this.state.searchData3[
                              this.state.index
                            ].currencies.map((cur, index) => " " + cur.name)}
                          </p>
                          <p>
                            {" "}
                            <span className="titleText" key={index}>
                              Languages:
                            </span>
                            {this.state.searchData3[
                              this.state.index
                            ].languages.map(
                              (lang, index) => " " + lang.name + ","
                            )}
                          </p>
                        </div>
                      </div>
                      <div className="border">
                        {" "}
                        <p className="titleText">Border Countries: </p>
                        {this.state.searchData3[this.state.index].borders.map(
                          (border, index) => (
                            <div id="border" key={index}>
                              {border}
                            </div>
                          )
                        )}
                      </div>
                      <button onClick={this.handleSubmit.bind(this)}>
                        Back
                      </button>
                    </div>
                  </li>
                ))}
              </ul>
            </div>
          )}
        </section>
      ),
      data: this.state.data,
    });
  }
  componentDidMount() {
    fetch("https://restcountries.eu/rest/v2/all")
      .then((res) => res.json())
      .then((data) => {
        this.setState({
          data,
        });
      });
  }

  handleSubmit() {
    this.setState({
      header: <Header />,
      body: (
        <Body
          handleSelect={this.handleSelect.bind(this)}
          handleSearch={this.handleSearch.bind(this)}
        />
      ),
      countries: "",
      data: this.state.data,
      index: [],
      region: (
        <Region
          indexHandler={this.indexHandler.bind(this)}
          data={this.state.data}
        />
      ),
      regionData2: [],

      searchData3: [],
    });
  }
  indexHandler(event) {
    const index = event.target.getAttribute("data-index");
    this.state.index.unshift(index);

    this.setState({
      header: <Header />,
      body: "",
      data: this.state.data,
      countries: (
        <div className="individual-card">
          <ul>
            {this.state.data.reduce((country, index) => (
              <li key={index} id="individual-li">
                <div id="individual-card">
                  <div id="individual-img">
                    <img
                      src={this.state.data[this.state.index].flag}
                      data-index={index}
                      onClick={this.props.indexHandler}
                      alt={country.name}
                      id="ind-image"
                    ></img>
                  </div>
                  <div id="individual-country-info">
                    <h3
                      data-index={index}
                      onClick={this.props.indexHandler}
                      id="test"
                    >
                      {this.state.data[this.state.index].name}
                    </h3>
                    <div>
                      <p>
                        <span className="titleText">Native Name:</span>{" "}
                        {this.state.data[this.state.index].nativeName}
                      </p>
                      <p>
                        <span className="titleText">Population:</span>{" "}
                        {this.state.data[this.state.index].population}
                      </p>
                      <p>
                        <span className="titleText">Region:</span>{" "}
                        {this.state.data[this.state.index].region}
                      </p>
                      <p>
                        <span className="titleText">Sub Region:</span>{" "}
                        {this.state.data[this.state.index].subregion}
                      </p>
                    </div>
                    <div>
                      <p>
                        <span className="titleText">Capital: </span>
                        {this.state.data[this.state.index].capital}
                      </p>
                      <p>
                        <span className="titleText" key={index}>
                          Top Level Domain:
                        </span>{" "}
                        {this.state.data[this.state.index].topLevelDomain.map(
                          (dom, index) => dom
                        )}
                      </p>
                      <p>
                        <span className="titleText" key={index}>
                          Currencies:
                        </span>{" "}
                        {this.state.data[this.state.index].currencies.map(
                          (cur, index) => " " + cur.name
                        )}
                      </p>
                      <p>
                        {" "}
                        <span className="titleText" key={index}>
                          Languages:
                        </span>
                        {this.state.data[this.state.index].languages.map(
                          (lang, index) => " " + lang.name + ","
                        )}
                      </p>
                    </div>
                  </div>
                  <div className="border">
                    {" "}
                    <p className="titleText">Border Countries: </p>
                    {this.state.data[this.state.index].borders.map(
                      (border, index) => (
                        <div id="border" key={index}>
                          {border}
                        </div>
                      )
                    )}
                  </div>
                  <button onClick={this.handleSubmit.bind(this)}>Back</button>
                </div>
              </li>
            ))}
          </ul>
        </div>
      ),
      region: "",
      index: [],
    });
  }

  render() {
    return (
      <ThemeProvider theme={this.state.isToggleOn ? darkTheme : lightTheme}>
        <GlobalStyles />
        <div className="container">
          {this.state.header}
          {this.state.body}
          {this.state.countries}
          {this.state.region}
        </div>
      </ThemeProvider>
    );
  }
}
class Header extends Component {
  render() {
    return (
      <div id="header">
        <section id="title">
          <h1>Info Of World Countries</h1>
        </section>
        <section id="toggle">
          <SwitchButton />
        </section>
      </div>
    );
  }
}

class Body extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
    };
  }

  render() {
    return (
      <div id="bottom-header">
        <section id="search">
          <input
            type="search"
            id="search"
            name="search"
            placeholder="search"
            onChange={this.props.handleSearch}
          />
        </section>
        <section id="select">
          <select
            name="Continents"
            id="Continents"
            onChange={this.props.handleSelect}
            defaultValue={"DEFAULT"}
          >
            <option value="DEFAULT" disabled hidden>
              Filter By Region
            </option>

            <option value="Europe">Europe</option>
            <option value="Asia">Asia</option>
            <option value="Africa">Africa</option>
            <option value="Americas">Americas</option>
            <option value="Oceania">Oceania</option>
          </select>
        </section>
      </div>
    );
  }
}
class Countries extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      button: "",
    };
  }
  componentDidMount() {
    fetch("https://restcountries.eu/rest/v2/all")
      .then((res) => res.json())
      .then((data) => {
        this.setState({
          data,
        });
      });
  }

  render() {
    return (
      <section id="countries">
        <div className="card">
          <ul>
            {this.state.data.map((country, index) => (
              <li key={index} id="all-country-li">
                <img
                  id="all-countries"
                  src={country.flag}
                  data-index={index}
                  onClick={this.props.indexHandler}
                  alt={country.name}
                ></img>
                <h3
                  data-index={index}
                  onClick={this.props.indexHandler}
                  id="test"
                >
                  {country.name}
                </h3>
                <p>Population: {country.population}</p>
                <p>Region: {country.region}</p>
                <p>Capital: {country.capital}</p>
              </li>
            ))}
          </ul>
        </div>
      </section>
    );
  }
}
class Region extends Component {
  constructor(props) {
    super(props);
    this.state = {
      regionData2: this.props.data,
    };
  }

  render() {
    return (
      <section id="countries">
        <div className="card">
          <ul>
            {this.props.data.map((country, index) => (
              <li key={index} id="all-country-li">
                <img
                  src={country.flag}
                  data-index={index}
                  onClick={this.props.indexHandler}
                  alt={country.name}
                ></img>
                <h3
                  data-index={index}
                  onClick={this.props.indexHandler}
                  id="test"
                >
                  {country.name}
                </h3>
                <p>Population: {country.population}</p>
                <p>Region: {country.region}</p>
                <p>Capital: {country.capital}</p>
              </li>
            ))}
          </ul>
        </div>
      </section>
    );
  }
}
class SwitchButton extends Component {
  constructor() {
    super();
    this.state = {
      isToggleOn: false,
      pic: <img id="toggle-img" src={moon} alt="moon"></img>,
    };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick() {
    if (this.state.isToggleOn) {
      this.setState({
        pic: <img id="toggle-img" src={moon} alt="sun"></img>,
      });
    } else {
      this.setState({
        pic: <img id="toggle-img" src={sun} alt="sun"></img>,
      });
    }

    this.setState(function (prevState) {
      return { isToggleOn: !prevState.isToggleOn };
    });
  }
  render() {
    return (
      <>
        <ThemeProvider theme={this.state.isToggleOn ? darkTheme : lightTheme}>
          <GlobalStyles />
          <div className="container">
            {this.state.header}
            {this.state.body}
            {this.state.countries}
            {this.state.region}
          </div>
        </ThemeProvider>
        <div id="#toggle " onClick={this.handleClick}>
          <p>{this.state.pic}</p>{" "}
          {/*<p>{this.state.isToggleOn ? "Light-Mode" : "Dark-Mode"}</p>}*/}
        </div>
      </>
    );
  }
}

export default App;
